:root {
  --antique-white: #faebd7;
  --gunmetal: #272d2d;
  --moonstone: #50b2c0;
}

body {
  background-color: var(--antique-white);
  color: var(--gunmetal);
}

h1, h2 {
  font-family: Montserrat Subrayada, sans-serif;
}

h3, h4, h5, h6 {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
}

p, a {
  font-family: Special Elite, serif;
}

#index_main {
  padding: 1em;
}

.category_header {
  font-size: 1.5em;
}

.sketch_index {
  flex-flow: wrap;
  flex: auto auto 1;
  justify-content: flex-start;
  align-items: baseline;
  gap: 1em;
  display: flex;
}

.sketch_card {
  border-radius: .5em;
  flex: 0 40ch;
  padding: .5em;
  transition: all .3s cubic-bezier(.25, .8, .25, 1);
  position: relative;
  overflow: hidden;
  box-shadow: 0 .1em .3em #0000001f, 0 .1em .2em #0000003d;
}

.sketch_card:hover {
  box-shadow: 0 .8em 1.2em #00000040, 0 .4em .4em #00000038;
}

.sketch_card_link {
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.sketch_card img {
  object-fit: cover;
  width: 100%;
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.sketch_card_title {
  margin: 0;
  padding: 0;
  font-size: 1.2em;
  font-weight: 600;
}

.sketch_main {
  background: var(--gunmetal);
  justify-content: center;
  align-items: center;
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  max-height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  overflow: scroll;
}

.sketch_main canvas {
  box-shadow: 0 .3em .6em #00000080;
}

.sketch_header {
  background: #00000059;
  padding: .5em;
  position: absolute;
  top: 0;
  left: 0;
}

.sketch_header:hover {
  background: #00000080;
}

.sketch_header a {
  color: var(--moonstone);
  font-size: 1.5em;
  text-decoration: none;
}

